import { mapActions } from "vuex";

export default {
    name: 'Register',
    data() {
        return {
            username: '',
            password: ''
        }
    },
    methods: {
        ...mapActions(['register']),
        onRegister() {
            this.register({username: this.username,password: this.password})
                .then( () => {
                    this.$router.push({path: '/'}) //自动跳转到首页上。
                })
        }
    }
}


